@import "../imports/ui-colours";

.container {
  margin-top: 60px;
  font-family: Arial, sans-serif;
  font-size: 14px;
  line-height: 1.42857142857143;
}

.comments ul ul {
  margin-top: 0;
  margin-left: 60px;
}

.comments li {
  margin-top: 0;
}

.comments .comment img {
  margin-right: 20px;
}

.comments .comment {
  padding: 6px;
  padding-top: 10px;
  border-left: 3px solid transparent;
  border-bottom: 1px solid @ui-border-color;
}

.comments .comment:hover {
  padding-top: 10px;
  background: @ui-comment-hover-background;
  border-left: 3px solid @ui-comment-hover-border;
}

.comment-options {
  display: none;
}

.comments .comment:hover .comment-options {
  display: block;
}

.comment-edited {
  color: @ui-comment-edit-color;
}